  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0); }

.wrapper {
  height: 100vh;
  height: calc(var(--vh, 1vh) * 100);
  position: relative;
  display: flex;
  flex-direction: column;
  overflow-x: hidden; }
  .wrapper--no-scrollbar {
    height: 100vh;
    height: calc(var(--vh, 1vh) * 100);
    -ms-overflow-style: none;
    scrollbar-width: none; }
    .wrapper--no-scrollbar::-webkit-scrollbar {
