.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  background-color: rgba(0, 0, 0, 0.6);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  -ms-overflow-style: none;
  scrollbar-width: none; }
  .modal::-webkit-scrollbar {
    display: none; }
  .modal__wrapper {
    position: relative;
    display: flex;
    flex-shrink: 0;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 100%;
    padding: 60px 40px; }
    @media (max-width: 767.98px) {
      .modal__wrapper {
        padding: 60px 16px; } }
  .modal__overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    cursor: pointer; }
  .modal__content {
    position: relative;
    width: 600px;
    padding: 40px;
    background-color: white; }
    @media (max-width: 767.98px) {
      .modal__content {
        width: 100%;
        padding: 20px; } }
  .modal__close-btn {
    position: absolute;
    top: 15px;
    right: 15px; }
  .modal--responsive .modal__content {
    width: 100%; }
  .modal--fit-content .modal__content {
    width: auto; }
    @media (max-width: 767.98px) {
      .modal--fit-content .modal__content {
        width: 100%; } }
  .modal--no-scale {
    transform: none; }
  .modal--preload {
    transition: none; }
  .modal--active {
    transform: scale(1);
    visibility: visible;
    opacity: 1;
    pointer-events: auto; }

/* v 0.0.1 */
.ts-panel {
  position: fixed;
  bottom: 20px;
  left: 20px;
  padding: 10px 10px 0;
  background-color: rgba(0, 160, 223, 0.5);
  border-radius: 3px;
  z-index: 999999999999;
  max-width: 70%;
  transition: all 0.3s ease; }

.ts-btn {
  border: 0;
  padding: 5px 10px;
  border-radius: 3px;
  cursor: pointer;
  color: #ffffff;
  background: #e72e8b;
  background: linear-gradient(4deg, #e72e8b 0%, #00a0df 100%);
  transition: all 0.3s ease;
  margin-right: 10px;
  margin-bottom: 10px;
  transition: all 0.3s ease; }

.ts-btn:hover {
  background: #e72e8b;
  background: linear-gradient(256deg, #e72e8b 0%, #00a0df 100%); }

.ts-btn:last-child {
  margin-right: 0; }

.ts-console {
  position: fixed;
  padding: 10px;
  border-radius: 3px;
  background-color: rgba(0, 160, 223, 0.8);
  bottom: 80px;
  left: 20px;
  color: #fff;
  width: 100%;
  max-width: 900px;
  min-width: 300px;
  white-space: pre;
  font-family: monospace;
  max-height: 80vh;
  overflow-y: scroll; }

@media (max-width: 1000px) {
  .ts-console {
    display: none; } }

.ts-panel--close {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: linear-gradient(4deg, #e72e8b 0%, #00a0df 100%); }

.ts-panel--close:hover {
  background: linear-gradient(256deg, #e72e8b 0%, #00a0df 100%); }

.ts-panel--close .ts-btn {
  display: none; }

.ts-panel--close .ts-console {
  display: none; }

